.card-container3{
    position: relative;
    .btn3{
        position: absolute;
        bottom: 10px;
       
        color: black;
       
        left: 50%;
        transform: translateX(-50%);
        overflow: hidden;
        width: 100%;
       @include sm{
        position: absolute;
        bottom: 10px;
       
        color: black;
       
        left: 50%;
        transform: translateX(-50%);
        overflow: hidden;
        width: 100%;
       }
       @include md{
        position: absolute;
        bottom: 10px;
       
        color: black;
       
        left: 50%;
        transform: translateX(-50%);
        overflow: hidden;
        width: 100%;
       } 
       @include xxl{
        position: absolute;
        bottom: 10px;
       
        color: black;
       
        left: 50%;
        transform: translateX(-50%);
        overflow: hidden;
        width: 65%;
        margin: 0 auto;
       } 
       .add-to-cart3{
        text-transform: uppercase;
        font-size: 13px;
        margin: 0 auto;
        text-align: center;
        @include sm{
            text-transform: uppercase;
            font-size: 13px;
            margin: 0 auto;
            text-align: center;
        }
        @include md{
            text-transform: uppercase;
            font-size: 13px;
            margin: 0 auto;
            text-align: center;
        }
        @include lg{
            text-transform: uppercase;
            font-size: 13px;
            margin: 0 auto;
            text-align: center;
        }
        @include xl{
            text-transform: uppercase;
            font-size: 13px;
            margin: 0 auto;
            text-align: center;
        }
        @include xxl{
            text-transform: uppercase;
            font-size: 13px;
            margin: 0 auto;
            text-align: center;
            padding: 4px 0;
            position: relative;
            overflow: hidden;
            &:hover{
                border: 1px solid rgb(0, 0, 0,0.1);
                background-color: rgb(0, 123, 196,0.9);
                color: #fff;
                border-radius: 3px;
                transition: background-color 0.4s ease;
               }
        }
       }
