    }
    .imgContainer3{
        position: relative;
        overflow: hidden;
        @include sm{
            .quickView3{
                display: none;
            }
        }
        @include xxl{
            .quickView3{
                display: none;
            }
            &:hover{
                .img{
                    transform: scale(1.1);
                    transition: 1.1s ease-in;
                }
                .quickView3{
                    display: block;
                    position: absolute;
                    background-color: rgb(0, 0, 0,0.6);
                    color: white;
                    top: 15px;
                    right: 18px;
                    padding: 4px;
                    border: 1px solid rgba(0,0,0,0.2);
                    border-radius: 10px;
                    transform: translateX(100%);
                    animation: slide 0.5s ease forwards;
                }
            }
        }
    }
    .stockAndPrice{
        .stock3{
            color: rgba(0,0,0,0.7);
        }
    }
}
@keyframes slide {
    0%{transform: translateX(100%);}
    100%{transform: translateX(0);}
}